import {all, call, delay, fork, put, takeLatest, throttle} from "redux-saga/effects";
import axios from "axios";
import {
	ADD_COMMENT_FAILURE,
	ADD_COMMENT_REQUEST,
	ADD_COMMENT_SUCCESS,
	ADD_POST_FAILURE,
	ADD_POST_REQUEST,
	ADD_POST_SUCCESS,
	LIKE_POST_FAILURE,
	LIKE_POST_REQUEST,
	LIKE_POST_SUCCESS,
	LOAD_HASHTAG_POSTS_FAILURE,
	LOAD_HASHTAG_POSTS_REQUEST, LOAD_HASHTAG_POSTS_SUCCESS,
	LOAD_POST_FAILURE,
	LOAD_POST_REQUEST,
	LOAD_POST_SUCCESS,
	//generateDummyPost, front
	LOAD_POSTS_FAILURE,
	LOAD_POSTS_REQUEST,
	LOAD_POSTS_SUCCESS, LOAD_USER_POSTS_FAILURE,
	LOAD_USER_POSTS_REQUEST, LOAD_USER_POSTS_SUCCESS,
	REMOVE_POST_FAILURE,
	REMOVE_POST_REQUEST,
	REMOVE_POST_SUCCESS,
	RETWEET_FAILURE,
	RETWEET_REQUEST,
	RETWEET_SUCCESS,
	UNLIKE_POST_FAILURE,
	UNLIKE_POST_REQUEST,
	UNLIKE_POST_SUCCESS,
	UPLOAD_IMAGES_FAILURE,
	UPLOAD_IMAGES_REQUEST,
	UPLOAD_IMAGES_SUCCESS,
} from "../reducers/post";
import {
	ADD_POST_TO_ME,
	REMOVE_POST_OF_ME,
} from "../reducers/user";

//import shortId from "shortid"; front

// restAPI
function retweetAPI(data) {
	return axios.post(`/post/${data}/retweet`)
}

function* retweet(action) {
	try {
		const result = yield call(retweetAPI, action.data)
		console.log('saga retweet :::', action.data)
		yield put({
			type: RETWEET_SUCCESS,
			data: result.data,
		})
		console.log('saga retweet :::', result)
	} catch (err) {
		console.error('saga retweet  ::: ', err)
		yield put({
			type: RETWEET_FAILURE,
			error: err.response.data
		})
	}
}

function uploadImagesAPI(data) {
	return axios.post('/post/images', data) // form data  {name:data}   json   
}

function* uploadImages(action) {
	try {
		const result = yield call(uploadImagesAPI, action.data)
		console.log('saga uploadImages ::: ', action.data)
		yield put({
			type: UPLOAD_IMAGES_SUCCESS,
			data: result.data
		})
		console.log('saga uploadImages ::: ', result)
	} catch (err) {
		console.error('saga uploadImages error::: ', err)
		yield put({
			type: UPLOAD_IMAGES_FAILURE,
			error: err.response.data
		})
	}
}

function likePostAPI(data) { // 3 
	return axios.patch(`/post/${data}/like`) // 4  , like & unlike  patch (    )
}

function* likePost(action) { // 1 
	try {
		const result = yield call(likePostAPI, action.data) // 2  
		console.log('likePost :::', result)
		yield put({
			type: LIKE_POST_SUCCESS,
			data: result.data, // back, postId, userId 
		})
		console.log('likePost :::', result)
	} catch (err) {
		console.error('likePost error::: ', err)
		yield put({
			type: LIKE_POST_FAILURE,
			error: err.response.data,
		})
	}
}

function unlikePostAPI(data) { // 3 
	return axios.delete(`/post/${data}/like`) // 4  
}

function* unlikePost(action) { // 1 
	try {
		const result = yield call(unlikePostAPI, action.data) // 2  
		console.log('unlikePost :::', result)
		yield put({
			type: UNLIKE_POST_SUCCESS,
			data: result.data, // back, postId,userId 
		})
		console.log('UNLIKEPost :::', result)
	} catch (err) {
		console.error('UNLIKEPost error::: ', err)
		yield put({
			type: UNLIKE_POST_FAILURE,
			error: err.response.data
		})
	}
}

function loadPostsAPI(lastId) { // 3 
	//return axios.get('/api/posts', data) // 4   front
	//  get       (), etc : limit=10&offset=10
	//     ,       
	// lastId  undefined   0
	return axios.get(`/posts?lastId=${lastId || 0}`) // 4   ,
}

function* loadPosts(action) { // 1 
	try {
		console.log('Sagas loadPosts ::: ', action.lastId)
		const result = yield call(loadPostsAPI, action.lastId) // 2  
		//yield delay(1000) front
		console.log('sagas loadPosts ::: ', result)
		yield put({
			type: LOAD_POSTS_SUCCESS,
			// data: generateDummyPost(10), // data 10  front
			data: result.data,
		})
	} catch (err) {
		console.error('saga loadPosts error: ', err)
		yield put({
			type: LOAD_POSTS_FAILURE,
			error: err.response.data,
		});
	}
}

function loadPostAPI(data) {
	return axios.get(`/post/${data}`)
}

function* loadPost(action) {
	try {
		const result = yield call(loadPostAPI, action.data)
		console.log('saga loadPost  ::: ', action.data)
		yield put({
			type: LOAD_POST_SUCCESS,
			data: result.data,
		})
		console.log('saga loadPost  ::: ', result)
	} catch (err) {
		console.error('saga loadPost  ::: ', err)
		yield put({
			type: LOAD_POST_FAILURE,
			error: err.response.data,
		})
	}
}


function addPostAPI(data) { // 3 
	//return axios.post('/api/post', data) // 4   front
	// data content  req.body.content   
	//return axios.post('/post', {content: data}) // 4  ,
	// form data  {content: data}    ,  data  
	return axios.post('/post', data) // 4  
}

function* addPost(action) { // 1 
	try {
		const result = yield call(addPostAPI, action.data) // 2  
		//yield delay(1000) front
		//const id = shortId.generate() // front   
		console.log('addPost :::', result)
		yield put({
			type: ADD_POST_SUCCESS,
			/*data: { // front dummy
				id,
				content: action.data,
			}*/
			data: result.data, // back,   
		})
		console.log('addPost :::', result)
		yield put({
			type: ADD_POST_TO_ME,
			// data: id, // front dummy
			data: result.data.id, // back
		})
	} catch (err) {
		console.error('saga addPost error::: ', err)
		yield put({
			type: ADD_POST_FAILURE,
			error: err.response.data
		})
	}
}

function removePostAPI(data) { // 3 
	//return axios.post('/api/post', data) // 4   front
	return axios.delete(`/post/${data}`) // 4   back //delete  data  post.id (PostCard.js)  
}

function* removePost(action) { // 1 
	try { //          ,
		const result = yield call(removePostAPI, action.data) // 2  
		//yield delay(1000) front
		console.log('removePost ::: ', action.data)
		yield put({
			type: REMOVE_POST_SUCCESS,
			data: result.data, //    id ,
		})
		console.log('removePost ::: ', result)
		yield put({
			type: REMOVE_POST_OF_ME,
			//data: action.data, front
			data: action.data,  // action   
		})
	} catch (err) {
		console.error('saga removePost error: ', err)
		yield put({
			type: REMOVE_POST_FAILURE,
			error: err.response.data
		})
	}
}

function addCommentAPI(data) { // 3 
	//    
	//return axios.post(`/api/post/${data.postId}/comment`, data) // 4   // front
	return axios.post(`/post/${data.postId}/comment`, data) // 4   // POST /post/1/comment
}

function* addComment(action) { // 1 
	try {
		const result = yield call(addCommentAPI, action.data) // 2   //back
		//yield delay(1000) front
		console.log('addComment  :::', action.data)
		yield put({
			type: ADD_COMMENT_SUCCESS,
			//data: action.data // front
			data: result.data,
		})
		console.log('addComment :::', result)
	} catch (err) {
		console.error('saga addComment error::: ', err)
		yield put({
			type: ADD_COMMENT_FAILURE,
			error: err.response.data
		})
	}
}

function loadUserPostsAPI(data, lastId) {
	return axios.get(`/user/${data}/posts?lastId=${lastId || 0}`)
}

function* loadUserPosts(action) {
	try {
		const result = yield call(loadUserPostsAPI, action.data, action.lastId)
		console.log('saga loadUserPosts  ::: ', action.data, action.lastId)
		yield put({
			type: LOAD_USER_POSTS_SUCCESS,
			data: result.data,
		})
		console.log('saga loadPostsSuccess ::: ', result)
	} catch (err) {
		console.error('saga loadUserPosts error ::: ', err)
		yield put({
			type: LOAD_USER_POSTS_FAILURE,
			error: err.response.data,
		})
	}
}

function loadHashtagPostsAPI(data, lastId) { //     
	// or  error, encode  
	return axios.get(`/hashtag/${encodeURIComponent(data)}?lastId=${lastId || 0}`)
}

function* loadHashtagPosts(action) {
	try {
		console.log(`loadHashtagPosts log :::`)
		const result = yield call(loadHashtagPostsAPI, action.data, action.lastId)
		console.log('saga loadHashtagPosts  ::: ', action.data, action.lastId)
		yield put({
			type: LOAD_HASHTAG_POSTS_SUCCESS,
			data: result.data,
		})
		console.log('saga loadHashtagPosts  ::: ')
	} catch (err) {
		console.error('saga loadHashtagPosts error ::: ', err)
		yield put({
			type: LOAD_HASHTAG_POSTS_FAILURE,
			error: err.response.data,
		})
	}
}

function* watchLoadUserPosts() {
	yield throttle(5000, LOAD_USER_POSTS_REQUEST, loadUserPosts)
}

function* watchLoadHashtagPosts() {
	yield throttle(5000, LOAD_HASHTAG_POSTS_REQUEST, loadHashtagPosts)
}

function* watchLoadPost() {
	yield takeLatest(LOAD_POST_REQUEST, loadPost)
}

function* watchRetweet() {
	yield takeLatest(RETWEET_REQUEST, retweet)
}

function* watchUploadImages() {
	yield takeLatest(UPLOAD_IMAGES_REQUEST, uploadImages)
}

function* watchLikePost() {
	yield takeLatest(LIKE_POST_REQUEST, likePost)
}

function* watchUnlikePost() {
	yield takeLatest(UNLIKE_POST_REQUEST, unlikePost)
}

function* watchLoadPosts() {
	yield throttle(5000, LOAD_POSTS_REQUEST, loadPosts)
}

function* watchAddPost() {
	yield takeLatest(ADD_POST_REQUEST, addPost)
}

function* watchRemovePost() {
	yield takeLatest(REMOVE_POST_REQUEST, removePost)
}

function* watchAddComment() {
	yield takeLatest(ADD_COMMENT_REQUEST, addComment)
}

export default function* postSaga() {
	yield all([
		fork(watchLoadPost),
		fork(watchRetweet),
		fork(watchUploadImages),
		fork(watchLikePost),
		fork(watchUnlikePost),
		fork(watchLoadUserPosts),
		fork(watchLoadHashtagPosts),
		fork(watchLoadPosts),
		fork(watchAddPost),
		fork(watchRemovePost),
		fork(watchAddComment),
	])
}