import React, {useCallback, useEffect} from 'react'
import {Form, Input, Button} from 'antd'
import Link from 'next/link'
import styled from 'styled-components'
import PropTypes from 'prop-types'
import useInput from '../hooks/useInput'
import {useDispatch, useSelector} from "react-redux";
import {loginAction, loginRequestAction} from "../reducers/user";

const ButtonWrapper = styled.div`
	margin-top: 10px;
`

const FormWrapper = styled(Form)`
	padding: 10px;
`

const LoginForm = () => {
	const dispatch = useDispatch()
	const {logInLoading, logInError} = useSelector((state) => state.user)
	const [email, onChangeEmail] = useInput('')
	const [password, onChangePassword] = useInput('')

	//  
	useEffect(()=>{
		if (logInError) {
			alert(logInError)
		}
	},[logInError])

	// const style = useMemo(()=>({ // styled components    useMemo 
	//   marginTop: 10
	// }), []);

	const onSubmitFrom = useCallback(() => {
		console.log('email : ' + email + ',' + ' password : ' + password)
		//setIsLoggedIn(true)
		dispatch(loginRequestAction({email, password})) //  create
	}, [email, password])

	return (
			<FormWrapper onFinish={onSubmitFrom}>
				{/*onFinish   e.preventDefault()  .*/}
				<div>
					<label htmlFor="user-email"></label>
					<br/>
					<Input name="user-email" type="email" value={email} onChange={onChangeEmail} required/>
				</div>
				<div>
					<label htmlFor="user-password"></label>
					<br/>
					<Input name="user-password" type="password" value={password} onChange={onChangePassword} required/>
				</div>
				<ButtonWrapper>
					<Button type="primary" htmlType="submit" loading={logInLoading}>
						 {/*htmlType   submit  */}
					</Button>
					<Link href="/signup">
						<a>
							<Button></Button>
						</a>
					</Link>
				</ButtonWrapper>
			</FormWrapper>
	)
}

/*LoginForm.propTypes = {
  setIsLoggedIn: PropTypes.func.isRequired,
}*/

export default LoginForm
