import {all, call, delay, fork, put, takeLatest} from "redux-saga/effects";
import axios from "axios";
import {
	CHANGE_NICKNAME_FAILURE,
	CHANGE_NICKNAME_REQUEST,
	CHANGE_NICKNAME_SUCCESS,
	FOLLOW_FAILURE,
	FOLLOW_REQUEST,
	FOLLOW_SUCCESS, LOAD_FOLLOWERS_FAILURE, LOAD_FOLLOWERS_REQUEST,
	LOAD_FOLLOWERS_SUCCESS, LOAD_FOLLOWINGS_FAILURE, LOAD_FOLLOWINGS_REQUEST, LOAD_FOLLOWINGS_SUCCESS,
	LOAD_MY_INFO_FAILURE,
	LOAD_MY_INFO_REQUEST,
	LOAD_MY_INFO_SUCCESS, LOAD_USER_FAILURE, LOAD_USER_REQUEST, LOAD_USER_SUCCESS,
	LOG_IN_FAILURE,
	LOG_IN_REQUEST,
	LOG_IN_SUCCESS,
	LOG_OUT_FAILURE,
	LOG_OUT_REQUEST,
	LOG_OUT_SUCCESS, REMOVE_FOLLOWER_FAILURE, REMOVE_FOLLOWER_REQUEST, REMOVE_FOLLOWER_SUCCESS,
	SIGN_UP_FAILURE,
	SIGN_UP_REQUEST,
	SIGN_UP_SUCCESS,
	UNFOLLOW_FAILURE,
	UNFOLLOW_REQUEST,
	UNFOLLOW_SUCCESS,
} from "../reducers/user";


function logInAPI(data) { // generator 
													//return axios.post('/api/login', data) //      // front

	//      // back
	//return axios.post('http://localhost:3065/user/login', data)

	// localhost  
	return axios.post('/user/login', data)
}

//  
// const l = logIn({type: 'LOG_IN_REQUEST', data: {id: 'rlaalsdn8@naver.com'}})
// 		l.next();
// 		l.next();
/////////////////
function* logIn(action) { // login action request  action  
	try { //   
		//yield delay(1000) // front
		const result = yield call(logInAPI, action.data) //     
		console.log('saga logIn ::', action.data)
		yield put({
			type: LOG_IN_SUCCESS,
			//data: action.data, // front
			data: result.data // (  ) back
		})
		console.log('saga logIn  ::', result)
	} catch (err) {
		console.error('logIn', err)
		yield put({ // put  dispatch  
			type: LOG_IN_FAILURE,
			error: err.response.data // (  )
		})
	}
}

function logOutAPI() {
	// return axios.post('/api/logout') front
	return axios.post('/user/logout')
}

function* logOut() {
	try { //   
		//yield delay(1000) front
		yield call(logOutAPI) //      back
		console.log('sagas:: logout ')
		yield put({
			type: LOG_OUT_SUCCESS,
		})
		console.log('sagas:: logout ')
	} catch (err) {
		console.error('front logOut : ', err)
		yield put({ // put  dispatch  
			type: LOG_OUT_FAILURE,
			error: err.response.data // (  )
		})
	}
}

function changeNicknameAPI(data) { // generator 
																	 //      // back
																	 //return axios.post('http://localhost:3065/user/login', data)
																	 // localhost  
	return axios.patch('/user/nickname', {nickname: data})
}

function* changeNickname(action) { // login action request  action  
	try { //   
		//yield delay(1000) // front
		const result = yield call(changeNicknameAPI, action.data) //     
		console.log('saga changeNickname ::', action.data)
		yield put({
			type: CHANGE_NICKNAME_SUCCESS,
			//data: action.data, // front
			data: result.data // (  ) back
		})
		console.log('saga changeNickname  ::', result)
	} catch (err) {
		console.error('changeNickname', err)
		yield put({ // put  dispatch  
			type: CHANGE_NICKNAME_FAILURE,
			error: err.response.data // (  )
		})
	}
}

function loadMyInfoAPI(userId) {
	//    
	return axios.get(userId ? `/user/${userId}` : '/user/', {
		withCredentials: true,//        
	}); //  ,  .
}

function* loadMyInfo(action) {
	try {
		const result = yield call(loadMyInfoAPI, action.data)
		console.log('saga loadMyInfo  :: ', action.data)
		yield put({
			type: LOAD_MY_INFO_SUCCESS,
			data: result.data,
			me: !action.data
		})
		console.log('saga loadMyInfo :: ', result)
	} catch (err) {
		console.error('saga loadMyInfo error :: ', err)
		yield put({
			type: LOAD_MY_INFO_FAILURE,
			error: err.response.data
		})
	}
}

function followAPI(data) { // data   id 
													 // return axios.post('/api/follow', data) front
	return axios.patch(`/user/${data}/follow`)
}

function* follow(action) {
	try {
		//yield delay(1000) front
		const result = yield call(followAPI, action.data)
		console.log('saga follow  : ', result)
		yield put({
			type: FOLLOW_SUCCESS,
			//data: action.data front
			data: result.data
		})
		console.log('saga follow  :: ', result)
	} catch (err) {
		console.error('saga follow error :: ', err)
		yield put({
			type: FOLLOW_FAILURE,
			error: err.response.data,
		})
	}
}

function unfollowAPI(data) {// data   id 
	//return axios.post('/api/unfollow', data) front
	return axios.delete(`/user/${data}/follow`)
}

function* unfollow(action) {
	try {
		const result = yield call(unfollowAPI, action.data)
		//yield delay(1000) front
		console.log('saga unfollow  :: ', action.data)
		yield put({
			type: UNFOLLOW_SUCCESS,
			//data: action.data front
			data: result.data
		})
		console.log('saga unfollow :: ', result)
	} catch (err) {
		console.error('saga unfollow error:: ', err)
		yield put({
			type: UNFOLLOW_FAILURE,
			error: err.response.data,
		})
	}
}

