import React, {useCallback} from 'react'
import Link from 'next/link'
import PropTypes from 'prop-types'
import {Menu, Input, Row, Col} from 'antd' // antd  Menu 
import styled, {createGlobalStyle} from 'styled-components'
import UserProfile from '../components/UserProfile'
import LoginForm from '../components/LoginForm'
import {useSelector} from 'react-redux'
import useInput from "../hooks/useInput";
import Router from "next/router";


const Global = createGlobalStyle`
	.ant-row {
		margin-right: 0 !important;
		margin-left: 0 !important;
	}

	.ant-col:first-child {
		padding-left: 0 !important;
	}

	.ant-col:last-child {
		padding-right: 0 !important;
	}
`

// antd   Input.Search  styled components   
const SearchInput = styled(Input.Search)`
	vertical-align: middle;
`

const AppLayout = ({children}) => {
	//const [isLoggedIn, setIsLoggedIn] = useState(false)
	//      isLoggedIn   
	//const { isLoggedIn } = useSelector((state) => state.user)
	const [searchInput, onChangeSearchInput] = useInput('')
	const {me} = useSelector((state) => state.user)
	
	const onSearch = useCallback(
			() => {
				Router.push(`/hashtag/${searchInput}`) // searchInput  
			},
			[searchInput],
	);
	

	return (
			<div>
				<Global/>
				<Menu mode="horizontal">
					<Menu.Item key="home">
						<Link href="/">
							<a></a>
						</Link>
					</Menu.Item>
					<Menu.Item key="profile">
						<Link href="/profile">
							<a></a>
						</Link>
					</Menu.Item>
					<Menu.Item key="mail">
						<SearchInput //    
								enterButton
								value={searchInput}
								onChange={onChangeSearchInput}
								onSearch={onSearch}
						/>
					</Menu.Item>
				</Menu>
				{/*   gutter*/}
				<Row gutter={8}>
					{/*antd design   24  */}
					<Col xs={24} md={6}>
						{/*dummy data*/}
						{/*{isLoggedIn ? <UserProfile setIsLoggedIn={setIsLoggedIn}/> : <LoginForm setIsLoggedIn={setIsLoggedIn} />}*/}
						{me ? <UserProfile/> : <LoginForm/>}
					</Col>
					<Col xs={24} md={12}>
						{children} {/* AppLayout  children*/}
					</Col>
					<Col xs={24} md={6}>
						<a href="https://qhrdmaaksen.github.io/" target={'_blank'} rel="noreferrer noopener">
							Made in Vitamin
						</a>
					</Col>
				</Row>
			</div>
	)
}

AppLayout.propTypes = {
	children: PropTypes.node.isRequired,
}

export default AppLayout
