//        
const reducer = (state = initialState, action) => produce(state, (draft) => {
	// immer  state  draft  , switch   break  
	switch (action.type) {
		case ADD_COMMENT_REQUEST:
			draft.addCommentLoading = true
			draft.addCommentDone = false
			draft.addCommentError = null
			break;
		case ADD_COMMENT_SUCCESS: {
			//action.data.content, postId, userId
			//const post = draft.mainPosts.find((v) => v.id === action.data.postId) //   post  front
			const post = draft.mainPosts.find((v) => v.id === action.data.PostId) //   post  bak
			//post.Comments.unshift(dummyComment(action.data.content)) //  post        FRONT
			post.Comments.unshift(action.data) //  post    back
			draft.addCommentLoading = false
			draft.addCommentDone = true
			break;
		}
		case ADD_COMMENT_FAILURE:
			draft.addCommentLoading = false
			draft.addCommentError = action.error
			break;
		case LIKE_POST_REQUEST:
			draft.likePostLoading = true;
			draft.likePostDone = false;
			draft.likePostError = null;
			console.log('reducer LIKE_POST_REQUEST ::')
			break;
		case LIKE_POST_SUCCESS: {
			// mainPosts  id   
			const post = draft.mainPosts.find((v) => v.id === action.data.PostId);
			//       
			post.Likers.push({id: action.data.UserId});
			draft.likePostLoading = false;
			draft.likePostDone = true;
			console.log('reducer LIKE_POST_SUCCESS ::')
			break;
		}
		case LIKE_POST_FAILURE:
			draft.likePostLoading = false;
			draft.likePostError = action.error;
			console.error('reducer LIKE_POST_FAILURE :::', action.error)
			break;
			/*               */
		case LOAD_USER_POSTS_REQUEST:
		case LOAD_HASHTAG_POSTS_REQUEST:
		case LOAD_POSTS_REQUEST:
			draft.loadPostsLoading = true;
			draft.loadPostsDone = false;
			draft.loadPostsError = null;
			break;
		case LOAD_USER_POSTS_SUCCESS:
		case LOAD_HASHTAG_POSTS_SUCCESS:
		case LOAD_POSTS_SUCCESS:
			draft.loadPostsLoading = false
			draft.loadPostsDone = true
			// action.data  dummy data  ,  
			// concat        
			//draft.mainPosts = action.data.concat(draft.mainPosts) front
			draft.mainPosts = draft.mainPosts.concat(action.data)
			//  50  
			//draft.hasMorePosts = draft.mainPosts.length < 50 front
			draft.hasMorePosts = action.data.length === 10
			break;
		case LOAD_USER_POSTS_FAILURE:
		case LOAD_HASHTAG_POSTS_FAILURE:
		case LOAD_POSTS_FAILURE:
			draft.loadPostsLoading = false
			draft.loadPostsError = action.error
			break;
		case ADD_POST_REQUEST:
			draft.addPostLoading = true;
			draft.addPostDone = false;
			draft.addPostError = null;
			break;
		case ADD_POST_SUCCESS:
			// draft.mainPosts.unshift(dummyPost(action.data)) // front dummy
			draft.mainPosts.unshift(action.data) // back  
			draft.addPostLoading = false
			draft.addPostDone = true
			draft.imagePaths = [] // add post  ,     
			break;
		case ADD_POST_FAILURE:
			draft.addPostLoading = false
			draft.addPostError = action.error
			break;
		case LOAD_POST_REQUEST:
			draft.loadPostLoading = true
			draft.loadPostDone = false
			draft.loadPostError = null
			console.log('reducer LOAD_POST_REQUEST :: ')
			break
		case LOAD_POST_SUCCESS:
			draft.singlePost = action.data //   
			draft.loadPostLoading = false
			draft.loadPostDone = true
			console.log('reducer LOAD_POST_SUCCESS :: ')
			break
		case LOAD_POST_FAILURE:
			draft.loadPostLoading = false
			draft.loadPostError = action.error
			console.error('reducer LOAD_POST_FAILURE :: ', action.error)
		case REMOVE_IMAGE:
			draft.imagePaths = draft.imagePaths.filter((v, i) => i !== action.data)
			console.log('reducer REMOVE_IMAGE ')
			break
		case RETWEET_REQUEST:
			draft.retweetLoading = true
			draft.retweetDone = false
			draft.retweetError = null
			console.log('reducer RETWEET_REQUEST ::')
			break
		case RETWEET_SUCCESS: {
			draft.retweetLoading = false
			draft.retweetDone = true
			draft.mainPosts.unshift(action.data) //    
			console.log('reducer RETWEET_SUCCESS ::')
			break
		}
		case RETWEET_FAILURE:
			draft.retweetLoading = false
			draft.retweetError = action.error
			console.error('reducer RETWEET_FAILURE ::', action.data)
			break
		case UNLIKE_POST_REQUEST:
			draft.unlikePostLoading = true;
			draft.unlikePostDone = false;
			draft.unlikePostError = null;
			console.log('reducer UNLIKE_POST_REQUEST ::')
			break;
		case UNLIKE_POST_SUCCESS: {
			const post = draft.mainPosts.find((v) => v.id === action.data.PostId);
			post.Likers = post.Likers.filter((v) => v.id !== action.data.UserId);
			draft.unlikePostLoading = false;
			draft.unlikePostDone = true;
			console.log('reducer UNLIKE_POST_SUCCESS ::')
			break;
		}
		case UNLIKE_POST_FAILURE:
			draft.unlikePostLoading = false;
			draft.unlikePostError = action.error;
			console.error('reducer UNLIKE_POST_FAILURE :::', action.error)
			break;
		case UPLOAD_IMAGES_REQUEST:
			draft.uploadImagesLoading = true
			draft.uploadImagesDone = false
			draft.uploadImagesError = null
			console.log('reducer UPLOAD_IMAGES_REQUEST ::')
			break
		case UPLOAD_IMAGES_SUCCESS:
			draft.imagePaths = action.data
			draft.uploadImagesLoading = false
			draft.uploadImagesDone = true
			console.log('reducer UPLOAD_IMAGES_SUCCESS ::')
			break
		case UPLOAD_IMAGES_FAILURE:
			draft.uploadImagesLoading = false
			draft.uploadImagesError = action.error
			console.error('reducer UPLOAD_IMAGES_FAILURE :::', action.error)
			break
		case REMOVE_POST_REQUEST:
			draft.removePostLoading = true
			draft.removePostDone = false
			draft.removePostError = null
			break;
		case REMOVE_POST_SUCCESS:
			draft.mainPosts = draft.mainPosts.filter((v) => v.id !== action.data.PostId) //   filter  
			draft.PostLoading = false
			draft.PostDone = true
			break;
		case REMOVE_POST_FAILURE:
			draft.removePostLoading = false
			draft.removePostError = action.error
			break;
		default:
			break;
	}
})
export default reducer
